* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  letter-spacing: 0.9px;
}

.login_child_first {
  width: 25%;
  border-radius: 4px;
  background: rgba(230, 230, 255, 0.5);
  margin: auto;
  margin-top: 80px;
  border: 1px solid rgb(235, 229, 229);
}
.login_child_first h6 {
  padding: 10px;
  font-size: medium;
  text-align: center;
  color: rgb(24, 23, 23);
  border-bottom: 1px solid rgb(235, 229, 229);
}

.login_child_first_form {
  border: 1px solid transparent;
  width: 90%;
  border-radius: 4px;
  margin: 2% 5%;
  display: block;
  text-align: left;
  padding-top: 10px;
  padding-bottom: 12px;
}

.login_child_first_form label {
  margin: 2px;
  width: 95%;
  font-weight: 300;
  color: rgb(0, 0, 0);
}
.login_child_first_form input {
  width: 100%;
  height: 30px;
  border: 1px solid rgb(185, 184, 184);
  border-radius: 7px;
  margin: 5px 0px;
  cursor: text;
  margin-bottom: 5%;
}
.login_child_first_form input:focus {
  padding-left: 10px;
  outline: 2px solid rgba(84, 84, 245, 0.9);
}
input::-webkit-input-placeholder {
  border: 1px solid;
}
input:-ms-input-placeholder {
  border: 1px solid;
}
input::-ms-input-placeholder {
  border: 1px solid;
}
input::placeholder {
  border: 1px solid;
}
.login_child_first_form p {
  padding: 10px;
  font-size: 16px;
  text-align: center;
  color: rgb(24, 23, 23);
}

.login_child_first_form_span {
  color: rgba(31, 31, 247, 0.9);
  font-weight: bolder;
  font-size: 11.5px;
  cursor: pointer;
  margin-left: 42%;
  border: 1px solid transparent;
}
.login_child_first_form_submit {
  width: 100%;
  height: 30px;
  border: 1px solid rgb(185, 184, 184);
  border-radius: 7px;
  margin: 5px 0px;
  background-color: rgba(36, 143, 36, 0.9);
  cursor: pointer;
  color: antiquewhite;
  font-weight: bolder;
  letter-spacing: 0.9px;
  margin-top: 20px;
}
.errorMessage{
  color: red;
  /* border: 1px solid; */
  width: 80%;
  margin-left: 10%;
  padding: 10px;
  text-align: center;
}
  .registration {
  width: 32vw;
  border: 1px solid rgb(218, 216, 216);
  border-radius: 5px;
  margin: auto;
  margin-top: 50px;
  background-color: rgba(230, 230, 255, 0.5);
}

.registration h5 {
  width: 100%;
  text-align: center;
  color: rgb(29, 28, 28);
  border-bottom: 1px solid rgb(226, 225, 225);
  font-weight: bold;
  font-size: medium;
  padding: 10px;
}

.registration_form {
  display: inline-block;
  width: 90%;
  margin: 5%;
  border: 1px solid transparent;
}
.registration_form_input {
  width: 100%;
  height: 6vh;
  margin: auto;
  margin-top: 10px;
  border: 1px solid rgb(192, 191, 191);
  border-radius: 10px;
  padding-left: 20px;
  background: white;
}
 .registration_form_input::-webkit-input-placeholder {
  color: rgb(102, 101, 101);
  border: none;
  }
 .registration_form_input:-ms-input-placeholder {
  color: rgb(102, 101, 101);
  border: none;
  }
 .registration_form_input::-ms-input-placeholder {
  color: rgb(102, 101, 101);
  border: none;
  }
 .registration_form_input::placeholder {
  color: rgb(102, 101, 101);
  border: none;
  } 
.registration_form_submit {
  width: 100%;
  height: 6vh;
  color:#FFF;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
  margin-top: 20px;
  border: 1px solid rgb(211, 209, 209);
  border-radius: 10px;
  background-color: rgba(36, 143, 36, 0.9);
  cursor: pointer;
  font-weight: bold;

}
.registration .info-text {
  text-align: center;
  font-weight: normal;
  font-size: 16px;
  padding: 10px;
  margin-top: 2%;
  border-top: 1px solid rgb(221, 218, 218);
} 

.task-page {
  background: #f0f2f5;
}
.task-section{
  margin-right: 18px;
}

table {
  margin-top: 20px;
  margin-right: 50px;
  cursor: pointer;
  border-collapse: collapse;
  text-align: justify;
  background: #fff;
  border: 1px solid #222;
}

tr:nth-child(even) {
  background:#FAFAFA;
  color: rgb(0, 0, 0);
}
tr {
  height: 7vh;
  text-align: center;
  border: 1px solid #DDD;
}
tr > th {
  width: 15vw;
  border-collapse: collapse;
}

.header-section{
  background: #fff;
  margin-left: 25px;;
  margin-right: 25px;
}

